body {
  max-width: 1024px;
  min-width: 375px;
  margin: auto;
}

text {
  font-family: 'Space Mono', monospace;
  font-weight: 700;
  color: #00494d;
  margin: auto;
  background-color: #c5e4e7;
}

.input {
  font-family: 'Space Mono', monospace;
  font-weight: 500;
  font-size: 24px;
  margin: 1rem;
  padding: 1rem;
}

.input .input-bar {
  background-color: #f4fafa;
  display: flex;
  justify-content: space-between;
  padding: 0.5rem;
  border-radius: 10px;
}

.input .input-bar__invalid {
  border: 2px solid red;
}

.input .input-bar input {
  border: none;
  outline: none;
  text-align: right;
  font-family: 'Space Mono', monospace;
  font-weight: 700;
  font-size: 24px;
  color: #00494d;
  background-color: #f4fafa;
}

.input .input-bar img {
  height: 20%;
  -ms-grid-row-align: center;
  align-self: center;
  padding-left: 1rem;
}

.input .tip-pad {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (1fr)[3];
      grid-template-columns: repeat(3, 1fr);
  -ms-grid-rows: (1fr)[2];
      grid-template-rows: repeat(2, 1fr);
}

.input .tip-pad input {
  color: white;
  font-family: 'Space Mono', monospace;
  outline: none;
  border: none;
  padding: 0;
}
