body {
  max-width: 1024px;
  min-width: 375px;
  margin: auto;
}

text {
  font-family: 'Space Mono', monospace;
  font-weight: 700;
  color: #00494d;
  margin: auto;
  background-color: #c5e4e7;
}

.right-side {
  font-family: 'Space Mono', monospace;
  font-weight: 500;
  font-size: 24px;
  margin: 1rem;
  padding: 1rem;
  background-color: #00494d;
  border-radius: 10px;
  margin: 2rem;
  padding: 1.5rem;
  flex-grow: 1;
}

.right-side .container {
  text-align: center;
}

.right-side .container button {
  color: white;
  cursor: pointer;
  font-family: 'Space Mono', monospace;
  font-weight: 500;
  -ms-grid-row-align: center;
  align-self: center;
  background-color: #26c0ab;
  margin: .5rem;
  padding: .5rem 1rem .5rem 1rem;
  border-radius: 5px;
  color: #00494d;
}

.right-side .container button:disabled {
  pointer-events: none;
  opacity: .4;
}

.display {
  display: flex;
  justify-content: space-between;
  color: #26c0ab;
  border-radius: 10px;
}

.display p {
  padding: 0;
  margin: .2rem;
  color: #c5e4e7;
  font-size: 10px;
}

.display img {
  color: #26c0ab;
}
